import React from 'react';
import styled from 'styled-components';
import { Window, WindowHeader, WindowContent, Button } from 'react95';
import Draggable from 'react-draggable';
import { FAQ_CONTENT } from '../documents/FAQ';
import { WHITEPAPER_CONTENT } from '../documents/Whitepaper';
import { ETHBUX_CONTENT } from '../documents/ETHBUX';

const WindowWrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 50;
`;

const DocumentWindow = styled(Window)`
  width: 600px;
  height: 80vh;
  max-height: 800px;
  display: flex;
  flex-direction: column;
`;

const DocumentContent = styled(WindowContent)`
  flex: 1;
  padding: 16px;
  background-color: white;
  font-family: 'Times New Roman', Times, serif;
  overflow-y: auto;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    width: 16px;
  }
  
  &::-webkit-scrollbar-track {
    background: #dfdfdf;
    border: 1px solid #c3c3c3;
  }
  
  &::-webkit-scrollbar-thumb {
    background: #c3c3c3;
    border: 1px solid #a9a9a9;
  }
  
  &::-webkit-scrollbar-thumb:hover {
    background: #a9a9a9;
  }
`;

const CloseButton = styled(Button)`
  position: absolute !important;
  right: 10px !important;
  top: 12px !important;
  width: 22px !important;
  height: 22px !important;
  padding: 0 !important;
  display: flex !important;
  align-items: center;
  justify-content: center;
  
  span {
    font-size: 18px;
    transform: translateY(-1px);
  }
`;

const DOCUMENT_CONTENT = {
  FAQ: FAQ_CONTENT,
  WHITEPAPER: WHITEPAPER_CONTENT,
  Technicals: ETHBUX_CONTENT
};

export function WordDocument({ fileName, onClose }) {
  return (
    <WindowWrapper>
      <Draggable handle=".window-header" bounds="parent" defaultPosition={{ x: window.innerWidth / 2 - 300, y: window.innerHeight / 2 - 400 }}>
        <DocumentWindow>
          <WindowHeader className="window-header">
            {fileName}.txt - Word
            <CloseButton onClick={onClose}>
              <span>×</span>
            </CloseButton>
          </WindowHeader>
          <DocumentContent>
            <div 
              style={{ whiteSpace: 'pre-wrap' }}
              dangerouslySetInnerHTML={{ __html: DOCUMENT_CONTENT[fileName] }}
            />
          </DocumentContent>
        </DocumentWindow>
      </Draggable>
    </WindowWrapper>
  );
} 